import React from "react";
import { Channel } from "./Twitch";

enum SortBy {
  Viewers = "Viewers",
  Name = "Name",
  Followers = "Followers",
  Game = "Game",
  Random = "Random",
}

enum FilterBy {
  Live = "Live",
  All = "All",
}

type SortButtonProps = {
  type: SortBy;
  selected: boolean;
  onClick: (type: SortBy) => void;
  sortDescending: boolean;
};

type SortFilterProps = {
  channels: Channel[];
  liveChannels: Channel[];
  onChange: (shownChannels: Channel[]) => void;
}

type SortFilterState = {
  filterBy: FilterBy;
  sortBy: SortBy;
  sortDescending: boolean;
}

export class SortFilterBar extends React.Component<SortFilterProps, SortFilterState> {
  state: SortFilterState = {
    filterBy: FilterBy.Live,
    sortBy: SortBy.Viewers,
    sortDescending: true,
  }

  componentDidMount() {
    setTimeout(
      () => this.props.onChange(this.sort(this.props.channels, this.props.liveChannels))
    , 500);
    setTimeout(
      () => this.props.onChange(this.sort(this.props.channels, this.props.liveChannels))
    , 1000);
    setTimeout(
      () => this.props.onChange(this.sort(this.props.channels, this.props.liveChannels))
    , 2000);
  }

  componentDidUpdate(prevProps: Readonly<SortFilterProps>, prevState: Readonly<SortFilterState>) {
    if (prevProps.channels !== this.props.channels || prevProps.liveChannels !== this.props.liveChannels ||
      prevState.filterBy !== this.state.filterBy || prevState.sortBy !== this.state.sortBy || prevState.sortDescending !== this.state.sortDescending) {
        this.props.onChange(this.sort(this.props.channels, this.props.liveChannels));
    }
  }

  sort(channels: Channel[], liveChannels: Channel[]): Channel[] {
    let shownChannels: Channel[] = [];
    if (this.state.filterBy === FilterBy.Live) {
      shownChannels = liveChannels;
    } else {
      shownChannels = channels;
    }
    shownChannels = SortChannels(shownChannels, this.state.sortBy);
    if (!this.state.sortDescending) {
      shownChannels = shownChannels.reverse();
    }
    if (this.state.sortBy === SortBy.Game) {
      shownChannels = shownChannels.filter(channel => channel.game !== "");
    }

    return shownChannels;
  }

  setFilter(filterBy: FilterBy) {
    if (filterBy === FilterBy.All && this.state.filterBy !== filterBy && this.state.sortBy === SortBy.Viewers) {
      this.setState({
        sortBy: SortBy.Followers,
        sortDescending: true,
      })
    };
    this.setState({
      filterBy: filterBy,
    });
  }

  setSort(sortBy: SortBy) {
    if (this.state.sortBy === sortBy) {
      this.setState({
        sortDescending: !this.state.sortDescending,
      });
    } else {
      this.setState({
        sortBy: sortBy,
        sortDescending: sortBy !== SortBy.Name && sortBy !== SortBy.Game,
      });
    }
  }

  render() {
    return (
        <div className="top">
          <div className="filterBy">
            <div className="optionDescription">Filter By</div>
            <div className="optionValues">
              {Object.values(FilterBy).map((key) => {
                return (
                  <div key={key}>
                    <FilterButton
                      onClick={() => this.setFilter(key)}
                      type={key}
                      selected={this.state.filterBy === key}
                    />
                  </div>
                );
              })}
            </div>
          </div>
          <div className="sortBy">
            <div className="optionDescription">Sort By&nbsp;&nbsp;</div>
            <div className="sortValues">
              {Object.values(SortBy).map((key) => {
                return (
                  <div key={key}>
                    <SortButton
                      onClick={() => this.setSort(key)}
                      type={key}
                      selected={this.state.sortBy === key}
                      sortDescending={this.state.sortDescending}
                    />
                  </div>
                );
              })}
            </div>
          </div>
        </div>
    )
  }
}

function SortButton(props: SortButtonProps) {
  const className = props.selected ? "buttonEnabled" : "buttonDisabled";
  const arrow = props.sortDescending ? "▼" : "▲";
  const showArrow = props.selected && props.type !== SortBy.Random;
  return (
    <span className="sortValue">
      <span className="sortButton">
        <button className={className} onClick={() => props.onClick(props.type)}>
          {props.type}
        </button>
      </span>
      <span className="sortArrow">
        <button onClick={() => props.onClick(props.type)}>
          {showArrow && arrow}
        </button>
      </span>
    </span>
  );
}

type FilterButtonProps = {
  type: FilterBy;
  selected: boolean;
  onClick: (type: FilterBy) => void;
};

function FilterButton(props: FilterButtonProps) {
  const className = props.selected ? "buttonEnabled" : "buttonDisabled";
  return (
    <button className={className} onClick={() => props.onClick(props.type)}>
      {props.type}
    </button>
  );
}

function SortChannels(channels: Channel[], sortBy: SortBy): Channel[] {
  switch (sortBy) {
    case SortBy.Followers:
      return channels.sort(sortByFollowers);
    case SortBy.Viewers:
      return channels.sort(sortByViewers);
    case SortBy.Name:
      return channels.sort(sortByChannel);
    case SortBy.Game:
      return channels.sort(sortByGame);
    case SortBy.Random:
      return sortByRandom(channels);
  }
}

function sortByViewers(a: Channel, b: Channel): number {
  if (a.stream === null && b.stream === null) {
    return b.followers - a.followers;
  }

  let aa = a.stream !== null ? a.stream.viewers : 0;
  let bb = b.stream !== null ? b.stream.viewers : 0;

  return bb - aa;
}

function sortByFollowers(a: Channel, b: Channel): number {
  return b.followers - a.followers;
}

function sortByChannel(a: Channel, b: Channel): number {
  return b.display_name.localeCompare(a.display_name);
}

function sortByGame(a: Channel, b: Channel): number {
  return b.game.localeCompare(a.game);
}

function sortByRandom<T>(array: T[]): T[] {
  for (let i = array.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [array[i], array[j]] = [array[j], array[i]];
  }

  return array
}