import React from "react";
//import lozad from "lozad";
import { Channel, LookupChannels, LookupLiveChannels } from "./Twitch";
import { SortFilterBar } from "./SortFilter";

type LiveProps = {};

type LiveState = {
  isLoaded: boolean;
  isError: boolean;

  channels: Channel[];
  liveChannels: Channel[];
  shownChannels: Channel[];

  observer: any; // lozad.Observer;
};
interface Vtuber {
  user_id: string;
  twitter: string;
  socials: { [key: string]: string };
}

type streamInfoT = {
  viewers: React.ReactElement;
  image: string;
};

class Live extends React.Component<LiveProps, LiveState> {
  state: LiveState = {
    isLoaded: false,
    isError: false,
    channels: [],
    liveChannels: [],
    shownChannels: [],
    //@ts-ignore
    observer: window.lozad(".lozad", { enableAutoReload: true }),
  };

  componentDidMount() {
    fetch(process.env.PUBLIC_URL + "/vtubers.json")
      .then((res) => res.json())
      .then((vtubers) => {
        this.updateChannels(vtubers);
        this.updateStreams(vtubers);
        setInterval(() => this.updateStreams(vtubers), 1000 * 60 * 10);
      });
  }

  componentDidUpdate() {
    //@ts-ignore
    this.state.observer.observe();
  }

  updateChannels(vtubers: Vtuber[]) {
    const ids = vtubers.map((vtuber) => vtuber.user_id);
    LookupChannels(ids)
      .then((channels) => {
        return channels.map((channel) => {
          let ch = vtubers.filter(
            (vtuber) => parseInt(vtuber.user_id) === channel._id
          )[0];
          channel.twitter = ch.twitter;
          channel.socials = ch.socials;
          return channel;
        });
      })
      .then((channels) => {
        this.setState({
          channels: mergeWithStreams(channels, this.state.liveChannels),
        });
      })
      .catch((err) => {
        if (!this.state.isLoaded) {
          this.setState({
            isError: true,
          });
        }
      });
  }

  updateStreams(vtubers: Vtuber[]) {
    const ids = vtubers.map((vtuber) => vtuber.user_id);
    LookupLiveChannels(ids)
      .then((liveChannels) => {
        this.setState({
          isLoaded: true,
          liveChannels: liveChannels,
          channels: mergeWithStreams(this.state.channels, liveChannels),
        });
      })
      .catch((err) => {
        console.log(err);
        if (!this.state.isLoaded) {
          this.setState({
            isError: true,
          });
        }
      });
  }

  updateShownChannels(shownChannels: Channel[]) {
    this.setState({
      shownChannels: shownChannels,
    });
  }

  render() {
    let loadingMsg: string = "Loading your anime waifus and husbandos now...";
    if (this.state.isError) {
      loadingMsg = "Error loading data :( Try refreshing";
    }
    if (!this.state.isLoaded) {
      return (
        <div id="live-channels" className="col s12 m10 l8 xl6">
          <div className="live-channel card grey-text lighten-5">
            {loadingMsg}
          </div>
        </div>
      );
    }

    return (
      <div className="col s12 m10 l8 xl6">
        <SortFilterBar
          onChange={(channels: Channel[]) => this.updateShownChannels(channels)}
          channels={this.state.channels}
          liveChannels={this.state.liveChannels}
        />
        <div id="live-channels">
          {this.state.shownChannels.map((channel) => {
            return (
              <div
                className="live-channel card grey-text lighten-5"
                key={channel._id}
              >
                <ChannelInfo channel={channel} />
                <StreamInfo channel={channel} />
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}

type ChannelProps = {
  channel: Channel;
};

function ChannelInfo(props: ChannelProps) {
  return (
    <div className="streamer">
      <div>
        <a href={props.channel.url}>
          <img
            alt={"Channel Logo for " + props.channel.display_name}
            id={props.channel.name + "-profile"}
            className="lozad profile-pic circle"
            data-src={props.channel.logo.replace("300x300", "150x150")}
          />
        </a>
        <div>
          <div>
            <a className="displayName" href={props.channel.url}>
              {props.channel.display_name}
            </a>
            {props.channel.partner && (
              <img
                alt="Twith partner badge"
                className="partnerBadge"
                src={process.env.PUBLIC_URL + "/partner.png"}
              />
            )}
          </div>
          <div>
            <strong>{props.channel.followers}</strong> followers
          </div>
          <div>
            {props.channel.twitter && (
              <a href={"https://twitter.com/" + props.channel.twitter}>
                <img
                  alt="Twitter logo"
                  className="twitterLogo"
                  src={process.env.PUBLIC_URL + "/twitter.png"}
                />
              </a>
            )}
            {props.channel.socials && props.channel.socials["youtube"] && (
              <a href={props.channel.socials["youtube"]}>
                <img
                  alt="Youtube logo"
                  className="youtubeLogo"
                  src={process.env.PUBLIC_URL + "/youtube.png"}
                />
              </a>
            )}
            {props.channel.socials && props.channel.socials["instagram"] && (
              <a href={props.channel.socials["instagram"]}>
                <img
                  alt="Instagram logo"
                  className="instagramLogo"
                  src={process.env.PUBLIC_URL + "/instagram.png"}
                />
              </a>
            )}
            {props.channel.socials && props.channel.socials["discord"] && (
              <a href={props.channel.socials["discord"]}>
                <img
                  alt="Discord logo"
                  className="discordLogo"
                  src={process.env.PUBLIC_URL + "/discord.png"}
                />
              </a>
            )}
          </div>
        </div>
      </div>
      <div className="description card grey darken-4">
        {props.channel.description.slice(0, 350)}
      </div>
    </div>
  );
}

function StreamInfo(props: ChannelProps) {
  const streamInfo: streamInfoT = getStreamInfo(props.channel);
  return (
    <div className="playing">
      <div className="previewContainer">
        {streamInfo.image && (
          <span>
            {false && (
              <img // can't get this to look right yet
                alt={"Boxart for " + props.channel.game}
                className="boxart"
                src={
                  "https://static-cdn.jtvnw.net/ttv-boxart/" +
                  props.channel.game +
                  "-90x120.jpg"
                }
              />
            )}
            {props.channel.stream !== null && (
              <span className="liveIndicator">Live</span>
            )}
            <a href={props.channel.url}>
              <img
                alt={"Stream preview for " + props.channel.display_name}
                id={props.channel.name + "-preview"}
                className="lozad"
                data-src={streamInfo.image}
              />
            </a>
          </span>
        )}
      </div>
      <div>
        <div className="streamInfo">
          <a
            href={"https://www.twitch.tv/directory/game/" + props.channel.game}
          >
            {props.channel.game}
          </a>{" "}
          <div>{streamInfo.viewers}</div>
        </div>
        <div className="divider" />
        <div>{props.channel.status}</div>
      </div>
    </div>
  );
}

function getStreamInfo(channel: Channel): streamInfoT {
  let streamInfo: streamInfoT = (() => {
    if (channel.stream) {
      return {
        viewers: (
          <span>
            <strong>{channel.stream.viewers}</strong> viewers
          </span>
        ),
        image: channel.stream.preview.large + "#" + Date.now(),
      };
    } else {
      let image = channel.video_banner;
      if (image !== null) {
        image = image.replace("1920x1080", "426x240");
      }
      return {
        viewers: <strong>Offline</strong>,
        image: image,
      };
    }
  })();

  return streamInfo;
}

function mergeWithStreams(
  channels: Channel[],
  liveChannels: Channel[]
): Channel[] {
  if (channels.length === 0) {
    return [];
  }
  channels.forEach((channel) => (channel.stream = null));
  liveChannels.forEach((liveChannel) => {
    let channel = channels.filter(
      (channel) => channel._id === liveChannel._id
    )[0];
    channel.stream = liveChannel.stream;
    liveChannel.twitter = channel.twitter;
    liveChannel.socials = channel.socials;
  });

  return channels;
}

export default Live;
