export interface StreamPreview {
  small: string;
  medium: string;
  large: string;
  template: string;
}

export interface Channel {
  mature: boolean;
  status: string;
  broadcaster_language: string;
  broadcaster_software: string;
  display_name: string;
  game: string;
  language: string;
  _id: number;
  name: string;
  created_at: Date;
  updated_at: Date;
  partner: boolean;
  logo: string;
  video_banner: string;
  profile_banner: string;
  profile_banner_background_color: string;
  url: string;
  views: number;
  followers: number;
  broadcaster_type: string;
  description: string;
  private_video: boolean;
  privacy_options_enabled: boolean;

  // Not from API
  twitter: string;
  socials: {[key:string]: string}
  stream: Stream | null; // Not actually in response
}

export interface Stream {
  _id: string;
  game: string;
  broadcast_platform: string;
  community_id: string;
  community_ids: string[];
  viewers: number;
  video_height: number;
  average_fps: number;
  delay: number;
  created_at: Date;
  is_playlist: boolean;
  stream_type: string;
  preview: StreamPreview;
  channel: Channel;
}

export interface StreamsResponse {
  streams: Stream[];
}

export interface ChannelsResponse {
  channels: Channel[];
}

function twitchHeaders(): Headers {
  return new Headers({
    Accept: "application/vnd.twitchtv.v5+json",
    "Client-ID": "rt0aq3avxhv3qoj9kqkvdoi48rrz7e",
  });
}

export async function LookupChannels(ids: string[]): Promise<Channel[]> {
  const channelChunks: string[][] = chunk(ids, 100);
  const promises: Promise<ChannelsResponse>[] = channelChunks.map((chunk) => {
    let request: Request = new Request(
      "https://api.twitch.tv/kraken/channels?id=" + chunk.join(","),
      {
        method: "GET",
        headers: twitchHeaders(),
      }
    );

    return fetch(request)
      .then(handleFetchError)
      .then(res => res.json());
  });

  return Promise.all(promises)
    .then(promises => promises.map(promise => promise.channels).flat())
}

export async function LookupLiveChannels(ids: string[]): Promise<Channel[]> {
  const channelChunks: string[][] = chunk(ids, 100);
  const promises: Promise<StreamsResponse>[] = channelChunks.map((chunk) => {
    let request: Request = new Request(
      "https://api.twitch.tv/kraken/streams?channel=" + chunk.join(","),
      {
        method: "GET",
        headers: twitchHeaders(),
      }
    );

    return fetch(request)
      .then(handleFetchError)
      .then(res => res.json());
  });

  return Promise.all(promises)
    .then(promises => promises.map(promise => promise.streams))
    .then(res => res.flat())
    .then(streams =>
      streams.map(stream => {
        stream.channel.stream = stream;
        return stream.channel;
      })
    );
}

function handleFetchError(response: Response) {
  if (!response.ok) {
    throw Error(response.statusText);
  }
  return response;
}

function chunk<T>(arr: T[], size: number): T[][] {
  return Array.from({ length: Math.ceil(arr.length / size) }, (v, i) =>
    arr.slice(i * size, i * size + size)
  );
};
